<template>
    <div>
        <!-- {{genealogyData}} -->
        <Tree :data="treeData"></Tree>
    </div>
</template>

<script>
import {getCompleteGenealogy} from '@/api/Member';
export default {
    data() {
        return {
            genealogyData: {},

            treeData: [],
        }
    },

    methods: {
        getCompleteGenealogy() {
            getCompleteGenealogy(1).then(res => {
                let data = res.data.payload;

                this.genealogyData = res.data.payload;
                this.treeData = [this.format(data)];
            }).catch(err => {
                this.$Message.error(err);
            });
        },

        format(data) {
            let obj = {};
            obj['title'] = data.name;
            obj['children'] = [];
            obj['expand'] = true;
            if (data.childrenList.length > 0) {
                data.childrenList.forEach(element => {
                    obj['children'].push(this.format(element));
                });
            }
            return obj;
        },
    },

    mounted() {
        this.getCompleteGenealogy();
    },

}
</script>

<style>

</style>


