import {get, baseUrl} from '@/utils/axiosUtils';

// 获取成员的详细信息
export const getMemberDetail = (id) => {
    return get(`https://hongstrak.github.io/static_page/genealogy/information/${id}.json`);
}

// 获取完整的族谱表
export const getCompleteGenealogy = (id) => {
    return get(`https://hongstrak.github.io/static_page/genealogy/completeGenealogy/${id}.json`);
}



